*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #fff;
  overflow-x: hidden;
}
:root{
  --color-131921 : #131921;
  --color-3b4149 : #3b4149;
  --color-febd69 : #febd69;
  --color-232f3e : #232f3e;
  --color-bf4800 : #bf4800;
  --color-f5f5f7 : #f5f5f7;
  --color-ededed: #ededed;
  --color-777777: #777777;
  --color-1c1c1b: #1c1c1b;
}
.button{
  background-color: var(--color-232f3e);
  color: white;
  padding: 8px 25px;
  font-size: 18px;
  border-radius:25px;
}

.button:hover{
  background-color: var(--color-febd69);
  color: #131921;
}